/*  Quiz template
*
*  Version:
*    1.0.0 - 2022/01/14
*/

@import '../base.scss';

.c-quiz-template {
  padding-top: 1.0625rem;
  padding-bottom: 1.875rem;

  &__pretitle {
    @include t-font--type-7;
    font-weight: 700;
    text-align: center;
  }

  &__title {
    text-align: center;
    margin-top: 0.5rem;
    text-transform: capitalize;
  }

  &__content {
    text-align: center;
    margin: 1.6875rem auto 0;
    max-width: 39.0625rem;
    width: 78%;
  }

  &__quiz-wrapper {
    margin-top: 4.0625rem;
  }

  &__quiz {
    border: 0.0625rem solid $s-color--background-tertiary;
    background-color: $s-color--background-septenary;
    padding: 0.9375rem 0.9375rem;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    &.is-true {
      .c-quiz-template__quiz-content {
        display: none;
      }

      .c-quiz-template__quiz-answer {
        display: block;
      }

      .c-quiz-template__quiz-button--false {
        opacity: 0.2;
      }
    }

    &.is-false {
      .c-quiz-template__quiz-content {
        display: none;
      }

      .c-quiz-template__quiz-answer {
        display: block;
      }

      .c-quiz-template__quiz-button--true {
        opacity: 0.2;
      }
    }

    &-icon {
      display: block;
      height: 4rem;
      width: 4rem;
      margin: 2.8125rem auto 0;
    }

    &-content {
      margin: 1.6875rem auto 0;
      line-height: 1.125rem;
      max-width: 16.75rem;
      width: 79%;
    }

    &-answer {
      @include t-font--type-7;
      display: none;
      text-align: center;
      margin: 1.6875rem auto 0;
      max-width: 39.0625rem;
      width: 78%;
    }

    &-buttons {
      cursor: pointer;
      margin-top: 2rem;
      margin-bottom: 2rem;
      justify-content: center;
      display: flex;
    }

    &-button {
      &:first-of-type {
        margin-right: 0.5rem;
      }

      width: 7.5rem;
      text-align: center;
      padding: 0.75rem 0;
      background-color: $s-color--background-secondary;
      color: $s-color--font-6;
    }
  }

  &__arrows {
    display: none;
  }

  @include t-media-query($s-small-up) {
    padding: 1.0625rem 2.5rem 2.875rem;

    &__quiz-wrapper {
      position: relative;
    }

    &__arrows {
      &.is-hidden {
        display: none;

        +.c-quiz-template__quizzes {
          width: 100%;
        }
      }

      display: block;
      width: 100%;
      @include t-absolute-center-y();
    }

    &__arrow {
      display: block;
      width: t-to-rem(42);
      height: t-to-rem(42);
      background: $s-color--background-octonary url('../../assets/icon-arrow-right-w.svg') no-repeat center;
      border-radius: 100%;
      background-size: t-to-rem(13);
      cursor: pointer;
      position: absolute;

      &.is-left {
        transform: rotate(180deg);
        left: 0;
      }

      &.is-right {
        right: 0;
      }
    }

    &__quizzes {
      margin: 0 auto;
      width: 81%;
      &.is-3{
        justify-content: center;
      }
    }

    &__quiz {
      &.is-3{
        &:not(:last-child) {
          margin-right: 50px;
        }
      }
    }
  }

  @include t-media-query($s-medium-up) {
    @include t-gutters;
    padding: 1.0625rem 0 2.875rem;

    &__arrows {
      display: block;
    }

    &__arrow {
      display: block;
      width: t-to-rem(42);
      height: t-to-rem(42);
      background: $s-color--background-octonary url('../../assets/icon-arrow-right-w.svg') no-repeat center;
      border-radius: 100%;
      background-size: t-to-rem(13);
      cursor: pointer;

      &.is-left {
        transform: rotate(180deg);
      }
    }

    &__title {
      margin-top: 1.0625rem;
    }

    &__content {
      margin-top: 1.8125rem;
    }

    &__quiz-wrapper {
      margin-top: 5.3125rem;
    }
  }

  @include t-media-query($s-large-up) {
    padding: 1.0625rem 0 3.875rem;

    &__quiz {
      width: 25.6875rem;
      height: 25.6875rem;

      &-icon {
        height: 6rem;
        width: 6rem;
        margin: 3rem auto 0;
      }

      &-content {
        margin: 1.75rem auto 0;
        line-height: 1.625rem;
      }
    }

    &__quizzes {
      margin: 0 auto;
      width: 85%;
    }
  }
}